* {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
  }
  

  h3 {
    font-size: 18px;
    margin-top: 20px;
    margin-bottom: 5px;
  }
  
  form {
    /* padding: 0 40px; */
    width: 200px;
  }
  
  form input {
    display: block;
    width: 20px;
    font-size: 12px;
    padding: 5px 5px;
    /* margin: 10px 0; */
    border-radius: 5px;
    border: 1px solid #ddd;
  }
  
  form input.error {
    border-color: red;
  }
  
  form button {
    display: block;
    width: 100%;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    border-radius: 5px;
    background-color: #84c00c;
    color: #fff;
    border: none;
    font-size: 11px;
    /* height: 40px; */
    margin-top: 30px;
  }
  
  form button:hover {
    background-color: #669509;
  }
  
  form button:disabled {
    background-color: #dbf99f;
    color: #333;
  }
  
  form button.small {
    display: inline-block;
    width: auto;
    padding: 0 5px;
    background-color: #777;
    font-size: 12px;
  }
  
  form button.small:hover {
    background-color: #999;
  }
  
  .shareholder {
    display: -ms-flexbox;
    display: flex;
    direction: row;
    -ms-flex-align: center;
        align-items: center;
  }
  
  .shareholder button {
    margin: 0;
    margin-left: 10px;
  }
  


